"use client";
import { useEffect, useState } from "react";
import Image from "next/image";

export default function ItemsPage() {
    const [products, setProducts] = useState([]);

    useEffect(() => {
        fetch("https://server-sage-gamma.vercel.app/api/products")
            .then(res => res.json())
            .then(data => setProducts(data))
            .catch(err => console.error(err));
    }, []);

    return (
        <div className="px-4 md:px-20 py-12 bg-green-50 min-h-screen">
            <h1 className="text-4xl font-bold text-green-800 mb-6">Our Items</h1>
            <div className="grid sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
                {products.map(item => (
                    <div
                        key={item._id}
                        className="bg-white rounded-lg shadow hover:shadow-lg hover:-translate-y-1 transition cursor-pointer overflow-hidden"
                    >
                        <div className="relative w-full h-48">
                            <Image
                                src={item.image || "/items/default.jpg"}
                                alt={item.title}
                                fill
                                className="object-cover"
                            />
                        </div>
                        <div className="p-4">
                            <h3 className="text-lg font-semibold mb-1">{item.title}</h3>
                            <p className="text-green-700 text-sm mb-2 line-clamp-2">
                                {item.shortDescription}
                            </p>
                            <p className="text-green-900 font-semibold mb-3">${item.price}</p>
                            <button className="w-full py-2 bg-green-600 text-white rounded hover:bg-green-700 transition">
                                Details
                            </button>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
}
